/** Radial Status **/
[role=img][aria-label]::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 0 2px #666;
  border-radius: 50%;
  z-index: 20;
  border: none !important;
}

.friendsTable-133bsv .friendsRow-2yicud .friendsColumnName-1zBOKm {
  padding-left: 15px;
}

[role=img][aria-label] foreignObject {
  mask: none;
}

[role=img][aria-label] foreignObject img {
  border-radius: 50%;
}

[role=img][aria-label] svg rect {
  display: none;
}

[role=img][aria-label*=Online]::before {
  box-shadow: 0 0 0 2.25px #32a852;
}

[role=img][aria-label*="Online via mobile"]::before {
  box-shadow: 0 0 0 2.25px #4285f4;
}

[role=img][aria-label*="Do Not Disturb"]::before {
  box-shadow: 0 0 0 2.25px #c00;
}

[role=img][aria-label*=Idle]::before {
  box-shadow: 0 0 0 2.25px #faa61a;
}

[role=img][aria-label*=Streaming]::before {
  box-shadow: 0 0 0 2.25px #6441a5;
}

[role=img][aria-label*=Offline]::before {
  box-shadow: 0 0 0 2.25px #4d4d4d;
}

[role=img][aria-label*=Invisible]::before {
  box-shadow: 0 0 0 2.25px #4d4d4d;
}